* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.info-container {
  display: flex;
  gap: 15px;
  align-items: baseline;
}

.info-title {
  font-weight: 700;
}

.list-item {
  display: flex;
  align-items: center;
  gap: 15px;
  font-weight: 500;
  margin-left: 10px;
}
